<template>
  <div>
    <!-- 上传按钮 -->
    <div>
      <input
        ref="excel-upload-input"
        class="excel-upload-input"
        type="file"
        accept=".js"
        @change="handleClick"
      >
      <div class="drop">
        <!-- @drop="handleDrop"
              @dragover="handleDragover"
        @dragenter="handleDragover"-->
        <v-btn
          color="info"
          :loading="loading"
          style="margin-left:16px;"
          size="mini"
          type="primary"
          @click="handleUpload"
        >选择上传文件</v-btn>

        <v-btn
          :loading="loading"
          @click="alert.snackbar = true"
          href="./test.xlsx"
          download="de.js"
        >下载上传模板</v-btn>
      </div>
    </div>
    <!-- ---------------------  -->
    <v-snackbar
      v-model="alert.snackbar"
      :right="true"
      :timeout="alert.timeout"
      :top="true"
      :multi-line="true"
    >
      {{ alert.text }}
      <v-btn color="pink" flat @click="alert.snackbar = false">确认</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
// import XLSX from 'xlsx'
export default {
  components: {
    // Breadcrumbs
  },
  props: {
    beforeUpload: Function, // eslint-disable-line
    onSuccess: Function // eslint-disable-line
  },
  data() {
    return {
      // 提示框
      alert: {
        text:
          "模板文件正在下载...如果长时间未响应或者下载失败请使用:Google Chrome/Firefox 重新下载  (如果浏览器提示安全问题，请忽略以继续下载！)",
        timeout: 10000,
        snackbar: false
      },
      loading: false,
      excelData: {
        header: null,
        results: null
      },
      e6: 1,
      items: [
        {
          text: "热更算法",
          disabled: false,
          to: "/algorithm"
        },
        {
          text: "ED算法上传",
          disabled: false,
          to: "/algorithm"
        }
      ]
    };
  },
  methods: {
    // 按钮触发
    handleUpload() {
      this.$refs["excel-upload-input"].click();
    },
    handleClick() {
      let data = {
        url: this.$refs["excel-upload-input"].value,
        files: this.$refs["excel-upload-input"].files[0]
      }
      this.onSuccess( data );
    }
  }
};
</script>

<style lang="less">
.excel-upload-input {
  display: none;
  z-index: -9999;
}
.drop {
  border: 2px dashed #bbb;
  width: 600px;
  height: 160px;
  line-height: 160px;
  margin: 0 auto;
  font-size: 24px;
  border-radius: 5px;
  text-align: center;
  color: #fff;
  position: relative;
}
.v-btn__content {
  line-height: 30px !important;
  height: 30px;
}
</style>
