<template>
  <div id="batchlmport">
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>

    <v-stepper v-model="e6" vertical>
      <v-stepper-step :complete="e6 > 1" step="1">
        请选择要上传的ED算法文件(.js)
        <small></small>
      </v-stepper-step>

      <v-stepper-content step="1">
        <v-card color="grey lighten-1" class="mb-5" height="190px">
          <div class="app-container">
            <Updata :on-success="handleSuccess" :before-upload="beforeUpload" v-if="hide__"/>
          </div>
        </v-card>
        <v-btn flat>选择文件以继续</v-btn>
      </v-stepper-content>
      <v-stepper-step :complete="e6 > 2" step="2">您上传的文件</v-stepper-step>
      <v-stepper-content step="2">
        <v-card color="grey lighten-1" class="mb-5">
          <code>已选中的文件： {{ tableData }}</code>
        </v-card>
        <v-btn color="primary" @click="e6 = 3">下一步</v-btn>
      </v-stepper-content>
      <v-stepper-step :complete="e6 > 3" step="3">再次确认以开始上传文件</v-stepper-step>
      <v-stepper-content step="3">
        <v-card color="grey lighten-1" class="mb-5" height="auto">
          <code>准备开始上传的文件： {{ tableData }}</code>
        </v-card>
        <v-btn color="primary" @click="upData">确认上传</v-btn>
        <!-- <v-btn flat>Cancel</v-btn> -->
      </v-stepper-content>

      <v-stepper-step step="4">上传数据结果</v-stepper-step>
      <v-stepper-content step="4">
        <v-card color="grey lighten-1" class="mb-5" height="200px">
          <div>
            <div class="drop">
              <p style="line-height: 30px;margin-bottom: 1px;">😂 ED算法文件上传成功！</p>
              <p style="line-height: 30px;margin-bottom: 1px;">字节：{{upD.file_size}}</p>
              <p style="line-height: 30px;margin-bottom: 1px;">时间：{{ upD.mtime | time }}</p>
              <p style="line-height: 30px;margin-bottom: 1px;">文件 MD5：{{ upD.file_md5 }}</p>
              <p style="line-height: 30px;margin-bottom: 1px;">
                <v-btn color="primary" :href="upD.url" target="_blank">查看文件</v-btn>
              </p>
            </div>
          </div>
        </v-card>
        <v-btn color="primary" @click="e6 = 1">继续操作</v-btn>
      </v-stepper-content>
    </v-stepper>
    <!-- 进度条 -->
    <v-dialog v-model="del_dialog" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          正在上传数据请勿刷新浏览器...
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
// import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Breadcrumbs from "../breadcrumbs/breadcrumbs";
import Updata from "./updata.vue";
import { setTimeout } from "timers";
// import Http from "../../server/http.js";
export default {
  components: {
    Breadcrumbs,
    Updata
  },

  data() {
    return {
      // 导入数据
      tableData: "",
      tableHeader: [],
      // 分页数据
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      e6: 1,
      del_dialog: false,
      items: [
        {
          text: "热更算法",
          disabled: false,
          to: "/algorithm"
        },
        {
          text: "ED算法",
          disabled: false,
          to: "/algorithm"
        }
      ],
      headers: [
        {
          text: "代理商ID",
          align: "center",
          sortable: false,
          value: "name"
        },
        {
          text: "设备底座sn",
          value: "calories",
          sortable: false,
          align: "center"
        },
        { text: "设备上盖sn", value: "fat", sortable: false, align: "center" },
        { text: "蓝牙", value: "carbs", sortable: false, align: "center" },
        { text: "操作", value: "carbs", sortable: false, align: "center" }
      ],
      file: null,
      hide__: true,
      upD: {
        file_size: "",
        mtime: ""
      }
    };
  },
  methods: {
    // 文件读取前执行
    beforeUpload(file) {
      window.console.log(file);
    },
    // 文件读取后执行
    handleSuccess(file) {
      if (file) {
        window.console.log(file.files);
        this.file = file.files;
        this.e6 = 2;
        this.tableData = `${file.files.name}`;
      }
    },
    // 下载模板
    download() {
      let url = "../../../assets/download/test.xlsx";
      window.location.href = url;
    },
    // 删除单条数据
    del(index) {
      if (index <= this.tableData.length) {
        this.tableData.splice(index, 1);
      }
    },
    // 上传操作
    upData() {
      this.hide__ = false;
      this.del_dialog = true;
      var formdata = new FormData();
      formdata.append("file", this.file);
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_upload_ed,
        headers: {
          "Content-Type": "multipart/form-data"
        },
        data: formdata
      })
        .then(response => {
          this.hide__ = true;
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message + ",JS文件已上传到服务端！",
                timeout: 3000,
                snackbar: true
            })
            this.upD = response.data.datas;
            setTimeout(() => {
              this.del_dialog = false;
              this.e6 = 4;
            }, 500);
          } else {
            this.del_dialog = false;
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  }
};
</script>

<style lang="less" scoped>
#batchlmport {
  .mb-5 {
    overflow-y: auto;
    text-align: center;
    padding: 15px 0;
  }
  code {
    text-align: left;
    display: block;
    padding-left: 50px;
  }
}
</style>
